.infoBox{
    flex:1;
    cursor:pointer
  
}
.infoBox:not(:last-child){
    margin-right: 10px;
}
.infoBox--selected{
    border-top: 10px solid greenyellow;
}
.infoBox--red{
    border-color: red;
}
.infoBox--blue{
    border-color: rgb(110, 52, 243);
}
.infoBox_cases{
    color: #cc1034;
    font-weight: 600px;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox_cases--green{
    color:lightgreen !important
}
.infoBox_cases--blue{
    color:rgb(38, 68, 243) !important
}
.infoBox_total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size:  0.8rem !important;
    margin-top: 15px !important;
}